.SingleQuestionView {
  margin: 0;
  padding: 0;
}

.PresentationView {
  padding: .5em;
  display: grid;
  grid-template-areas: "nav collapse slide";
  grid-template-columns: 1.5in .5em 1fr;
  grid-template-rows: 1fr;
  margin: 0;
  padding: 0;
}

.Nav {
  grid-area: nav;
  margin: 0;
  padding: .5em 0 .5em .5em;
  color: var(--ui-fg-1, black);
  display: flex;
  flex-flow: column;
  justify-content: flex-end;
  overflow: hidden;
}

.Nav *,
[ui-theme] .Nav * {
  font-size: 1.2em;
  margin: .2em 0;
}

.NavButton {
  border-radius: .5em;
  outline: none;
  background-color: white;
}
.NavButton:hover {
  border-color: #777;
}
.NavButton:disabled {
  border-color: unset;
}
.NavButton,
[ui-theme] .NavButton {
  font-size: 1.1em;
  padding: 1em 1em;
}

.Slide {
  grid-area: slide;
  padding: 1em;
  overflow: scroll;
  position: relative;
  background-color: var(--ui-page, white);
}

.Answer {
  position: absolute;
  top: 1cm;
  left: 1cm;
  outline: none;
  border-radius: 4px;
  border: 1px solid var(--ui-border-1, #999);
  background-color: var(--ui-page, white);
  padding: 1cm;
  box-shadow: 1px 1px 5px black;
}

.ArrowButtonContainer {
  display: flex;
  width: auto;
  justify-content: space-evenly;
}

.ArrowButton {
  --inactive-color: var(--ui-fg-2, black);
  --button-inner: .75cm;
  width: .7cm;
  height: 1.5cm;
  padding: 1mm;
  display: inline-block;
  position: relative;
  color: white;
  text-decoration: none;
  margin: 3mm;
}

.ArrowButton:after {
  content: '';
  display: block;
  position: absolute;
  width: 0;
  height: 0;
  border-right: var(--button-inner) solid transparent;
  border-left: var(--button-inner) solid transparent;
  border-bottom: var(--button-inner) solid transparent;
  border-top: var(--button-inner) solid transparent;
  transform: scale(1.5,1);
  opacity: .6;
}
a:hover.ArrowButton:after {
  opacity: .9;
}
a:active.ArrowButton:after {
  opacity: .3;
}
a.ArrowButtonDisabled:after {
  opacity: 0.05;
}
a:hover.ArrowButtonDisabled:after {
  opacity: 0.05;
}

a.ArrowButtonLeft:after {
  transform-origin: right;
  right: 0;
  border-right-color: var(--inactive-color);
}
a.ArrowButtonRight:after {
  transform-origin: left;
  left: 0;
  border-left-color: var(--inactive-color);
}

.PresentationView.Collapsed {
  grid-template-columns: 0 4mm 1fr;
}
.Collapsed .Nav {
  display: none;
}

.Collapse {
  grid-area: collapse;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  border-right: 1px solid var(--ui-border-1, #999);
  overflow: visible;
}
.Collapse:hover {
  background-color: var(--ui-border-1, #bbb);
}
a.CollapseButton {
  --inactive-color: var(--ui-fg-2, black);
  --button-inner: 2mm;
  width: 1mm;
  height: 0;
  padding: 0mm;
  margin: 0mm;
  transform: scale(0.7,1.8);
  overflow: visible;
}

a.CollapseButton:after {
  right: 2px;
  transform-origin: right;
  border-right-color: var(--inactive-color);
}
.Collapsed a.CollapseButton:after {
  left: 2px;
  transform-origin: left;
  border-right-color: transparent;
  border-left-color: var(--inactive-color);
}

@media print {
  .PresentationView {
    grid-template-areas: "slide";
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
  }
  .Nav {
    display: none;
  }
  .Collapse {
    display: none;
  }
}