:root {
  /*--vg-anglemark-color: #6688ff;*/
  --vg-anglemark-color: #ff6688;
  --vg-anglemark-fill-opacity: 0.2;
  --vg-asymptote-color: #888888;
  --vg-asymptote-width: 0.6mm;
  --vg-axis-color: #111111;
  --vg-axis-width: 0.3mm;
  --vg-fill-opacity: 0.2;
  --vg-function-stroke-1: #ff3600;
  --vg-function-stroke-2: #0080d9;
  --vg-function-stroke-width: 0.4mm;
  --vg-geom-stroke-1: #000000;
  --vg-geom-stroke-2: #0055cc;
  --vg-geom-stroke-width: 0.3mm;
  --vg-geom-marker-color: #000000;
  --vg-grid-color: #888888;
  --vg-grid-width: 0.1mm;
  --vg-label-font-size: 1em;
  --vg-stroke-opacity: 1;
}

.svgplot-canvas {
  transform: var(--vg-transform, none);
  transform-origin: top left;
}

.svgplot-axis {
  color: var(--vg-axis-color);
  stroke-width: var(--vg-axis-width);
}

.svgplot-grid {
  color: var(--vg-grid-color);
  stroke-width: var(--vg-grid-width);
}

.svgplot-marker {
  fill-opacity: var(--vg-stroke-opacity);
}

.svgplot-label {
  color: black;
  font-size: var(--vg-label-font-size);
  white-space: nowrap;
}

/************
 STYLES 
 ************/

.vg-style-asymptote {
  color: var(--vg-asymptote-color);
  stroke-width: var(--vg-asymptote-width);
  stroke-dasharray: 2mm 1mm;
}

.vg-style-function {
  color: var(--vg-function-stroke-1);
  stroke-width: var(--vg-function-stroke-width);
  fill-opacity: var(--vg-fill-opacity);
}

.vg-style-function.vg-style-primary {
  color: var(--vg-function-stroke-1);
}

.vg-style-function.vg-style-secondary {
  color: var(--vg-function-stroke-2);
}

.vg-style-geometry {
  color: var(--vg-geom-stroke-1);
  stroke-width: var(--vg-geom-stroke-width);
  fill-opacity: var(--vg-fill-opacity);
}
.vg-style-geometry .svgplot-marker {
  fill: var(--vg-geom-marker-color);
}
.vg-style-geometry.vg-style-primary {
  color: var(--vg-geom-stroke-1);
}
.vg-style-geometry.vg-style-secondary {
  color: var(--vg-geom-stroke-2);
}

.vg-style-scatterplot {
  stroke-dasharray: 0 1;
  marker-start: var(--svgplot-marker-point);
  marker-mid: var(--svgplot-marker-point);
  marker-end: var(--svgplot-marker-point);
}

.vg-style-anglemark {
  color: var(--vg-anglemark-color);
  fill: currentColor;
  fill-opacity: var(--vg-anglemark-fill-opacity);
}

.vg-style-anglemark-right {
  stroke-linejoin: miter;
}

.vg-style-anglemark-congruent {
  color: var(--vg-geom-marker-color);
  fill: none;
}

.vg-style-dashed {
  stroke-dasharray: 2mm 1mm;
}

.vg-style-nostroke {
  stroke-width: 0;
}

.vg-style-circuit {
  color: black;
  stroke-width: .3mm;
  stroke-linecap: round;
}

.vg-style-black { color: #000000; }
.vg-style-gray { color: #999999; }
.vg-style-blue { color: #0080d9; }
.vg-style-green { color: #44aa66; }
.vg-style-orange { color: #f58518; }
.vg-style-pink { color: #f87199; }
.vg-style-purple { color: #cc66aa; }
.vg-style-red { color: #ff3600; }