:root {
  --ui-font-family: -apple-system, system-ui, BlinkMacSystemFont, 'Segoe UI', Roboto, 
    'Open Sans', Ubuntu, Helvetica, Arial, sans-serif;
  --ui-font-family-monospace: 'SF Mono', Menlo, 'Dejavu Sans Mono', 
    'Roboto Mono', 'Fira Mono', Consolas, Courier, 'katex_typewriter', 
    'Ubuntu Mono', monospace;
  --ui-font-size: 9pt;
}