
.FullModal > * {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  border: none;
  padding: 0;
  background: var(--ui-bg-1, #eee);
  outline: none;
}

.Popup {
  display: flex;
  justify-content: center;
  outline: none;
  padding: 1em;
}

.Popup > * {
  border-radius: .5em;
  border-color: var(--ui-border-1, #999);
  background: var(--ui-bg-1, #eee);
  padding: 1em;
  bottom: auto;
  right: auto;
  box-shadow: 0 0 1em black;
}

.Popup .PopupButtons {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 1em;
}

.Import {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.ImportDragbox {
  flex: 1 1 100%;
  height: 100%;
  border-radius: .5em;
  border-width: .3em;
  border-style: dashed;
  border-color: var(--ui-fg-2, black);
  background-color: var(--ui-border-0, rgba(210,210,210));
  opacity: 0.8;
  color: var(--ui-fg-1, black);
  font-family: katex_typewriter, sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.ImportDragboxDragover {
  border-color: var(--ui-highlight-bg, blue);
  color: var(--ui-highlight-bg, blue);
  background-color: var(--ui-border-1, rgb(255,255,255));
}